import React, { useState,useContext } from 'react'
import axios from 'axios'
import { useNavigate } from 'react-router-dom'
import { SnackContext,UserProContext } from "../../context/context"

export const useOtp = () => {
    const [state,setState]=useState({})
    const {setSnack}=useContext(SnackContext);
    const {userProfile,setUserProfile}=useContext(UserProContext)
    const [loading,setLoading] = useState()
    let navigate = useNavigate()
    const handleChange=(otp)=>{
        setState({otp})
    }   
    const handleSubmit=e=>{
        setLoading(true)
        e.preventDefault();
        // submitCallback();
        if (state.otp.length === 0) {
            alert("Please enter OTP!!")
        } else if (state.otp.length < 6) {
            alert("otp length should be 6 characters...")
           
        } else {
            const uname = localStorage.getItem("uname");
            const pass = localStorage.getItem("pass");
            const role = localStorage.getItem("role");
            const formdata = new FormData();
            formdata.append("otp", state.otp);
            formdata.append("uname", uname);
            formdata.append("pass", pass);
            formdata.append("cat_id", role);
            axios.post("/api/validate_otp", formdata).then(function (res) {
                if (res.data.status === true) {
                    console.log(res.data.data,"data");

                    console.log(res.data.data.token,"tkn");
                    console.log(res.data.data.user_mail,"tkn");

                    localStorage.setItem("tkn", res.data.data.token)
                    setLoading(false);
                    setSnack({
                        message: res.data.msg,
                        type: "success",
                        open: true,
                    });
                    setUserProfile(res.data.data);
                    navigate("/dashboard")
                } else {
                   console.log("Error")

                }
            });

        }
    };
  
  return [state,handleChange,handleSubmit,loading]
}

